@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

body {
  font-family: "Courier New", Courier, monospace;
  background-color: rgb(182, 180, 245);
  color: #311b57;
  margin: 0;
}

.bg-dark {
  background-color: #311b57;
  color: rgb(182, 180, 245);
}

.bg-primary {
  background-color: rgb(171, 169, 244);
  color: #311b57;
}

.bg-secondary {
  background-color: rgb(97, 91, 172);
  color: #311b57;
}

.bg-warning {
  background-color: rgb(255, 178, 106);
  color: #5a3b0e;
}

.bg-danger {
  background-color: rgb(255, 109, 109);
