  padding: 0;
}

small {
  font-size: 0.8rem;
}

a {
  text-decoration: none;
  color: #5f37cb;
}

a:hover:not(.text-shadow-none) {
  text-shadow: #311b57 0px 0px 8px;
}

.text-decoration-none {
  text-decoration: none;
  color: #311b57;
}

.text-shadow-none {
